/* Styling for the Navbar */
.navbar {
    background-color: hsl(0, 0%, 100%); /* Set your desired background color */
    padding: 10px 0; /* Add padding to top and bottom */
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2); /* Add a subtle box shadow */
    position: fixed; /* Fix the navbar to the top of the viewport */
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100; /* Set a high z-index to ensure it's above other content */
  }
  
  /* Styling for the container */
  .container {
    max-width: 1200px; /* Set the maximum width of the container */
    margin: 0 auto; /* Center the container horizontally */
    padding: 0 15px; /* Add horizontal padding to the container */
  }
  
  /* Styling for the row */
  .row {
    display: flex; /* Use flexbox to create a row layout */
    justify-content: space-between; /* Space items evenly in the row */
    align-items: center; /* Align items vertically in the row */
  }
  
  /* Styling for the logo */
  .logo {
    margin: 0; /* Remove default margin */
    font-size: 24px; /* Set the font size of the logo text */
    font-weight: bold; /* Optionally set font weight */
  }
  
  /* Styling for the navigation bar (ul) */
  .bar {
    list-style: none; /* Remove list bullets */
    margin: 0; /* Remove default margin */
    padding: 0; /* Remove default padding */
    display: flex; /* Use flexbox to create a horizontal list */
  }
  
  /* Styling for the navigation bar items (li) */
  .bar li {
    margin-right: 20px; /* Add space between each navigation item */
  }
  
  /* Styling for the active link */
  .active {
    font-weight: bold; /* Optionally highlight the active link */
    color: #ff0000; /* Set the color for the active link */
    /* Add any other styles you want for the active link */
  }
  
  /* Styling for the burger button container */
  .button {
    display: none; /* Hide the button container by default */
  }
  
  /* Styling for the burger button lines */
  .burger {
    width: 30px; /* Set the width of each line */
    height: 4px; /* Set the height of each line */
    background-color: #333; /* Set the color of each line */
    margin: 6px 0; /* Add spacing between each line */
  }
  
  /* Optional: Add a media query to display the burger button on smaller screens */
  @media (max-width: 768px) {
    .button {
      display: block; /* Show the button container on smaller screens */
      cursor: pointer; /* Change the cursor to indicate it's clickable */
    }
    .bar {
      display: none; /* Hide the navigation links on smaller screens */
    }
    .bar.active {
      display: flex; /* Show the navigation links when the button is clicked */
      flex-direction: column; /* Stack the links vertically */
      background-color: #ffffff; /* Set the background color for the mobile menu */
      position: absolute; /* Position the mobile menu */
      top: 100%; /* Position it below the navbar */
      left: 0;
      width: 100%;
      box-shadow: 0 0 5px rgba(0, 0, 0, 0.2); /* Add a box shadow for a card-like effect */
      padding: 15px 0; /* Add padding to the mobile menu */
    }
    .bar li {
      margin-right: 0; /* Reset the margin on the mobile links */
      margin-bottom: 10px; /* Add spacing between each mobile link */
    }

    /* Styling for the div with className "about-scroll" */
.about-scroll {
    margin-top: 15rem; /* Set the top margin to create space */
    width: 100%; /* Set the width to 100% */
    height: 10px; /* Set the height to 10px */
    background-color: #f0f0f0; /* Set the background color */
    /* Add any other styles you want for the "about-scroll" div */
  }

  /* Styling for the div with className "col-md-6 text-center" containing the image */
.col-md-6.text-center {
    flex: 1; /* Allow the column to grow and fill available space */
    display: flex; /* Use flexbox to center the image */
    justify-content: center;
    align-items: center;
    /* Add any other styles you want for the image container */
  }

  /* Styling for the image with className "img-fluid" */
.img-fluid {
    max-width: 100%; /* Set the maximum width of the image to fit its container */
    height: auto; /* Automatically adjust the height to maintain aspect ratio */
    /* Add any other styles you want for the image */
  }
  
  /* Styling for the div with className "col-md-6" containing the text */
  .col-md-6 {
    flex: 1; /* Allow the column to grow and fill available space */
    padding-left: 20px; /* Add left padding to create space between image and text */
    /* Add any other styles you want for the text container */
  }
  
  /* Styling for the h2 with className "main-title about-h2" */
  .main-title.about-h2 {
    font-size: 28px; /* Set the font size of the heading */
    margin-bottom: 20px; /* Add bottom margin to create space below the heading */
    /* Add any other styles you want for the heading */
  }
  
  /* Styling for the paragraph with className "main-p" */
  .main-p {
    font-size: 16px; /* Set the font size of the paragraph */
    line-height: 1.6; /* Set the line-height for better readability */
    /* Add any other styles you want for the paragraph */
  }

  /* Styling for the footer */
footer {
    background-color: #21793d; /* Set the background color for the footer */
    padding: 30px 0; /* Add padding to the top and bottom of the footer */
  }
  
  /* Styling for the container */
  .container {
    background-color: #21793d; /* Set the background color for the footer */

    max-width: 1200px; /* Set the maximum width of the container */
    margin: 0 auto; /* Center the container horizontally */
  }
  
  /* Styling for the rows in the side1 and side2 */
  .row {
    display: flex; /* Use flexbox to create a row layout */
    justify-content: space-between; /* Space items evenly in the row */
  }
  
  /* Styling for the side1 */
  .side1 {
    flex: 1; /* Allow the side1 to grow and fill available space */
  }
  
  /* Styling for the side2 */
  .side2 {
    flex: 1; /* Allow the side2 to grow and fill available space */
  }
  
  /* Styling for the logo */
  .logo {
    font-size: 24px; /* Set the font size of the logo text */
    font-weight: bold; /* Optionally set font weight */
    margin-bottom: 20px; /* Add bottom margin to create space below the logo */
  }
  
  /* Styling for the paragraph with className "footer-text" */
  .footer-text {
    font-size: 14px; /* Set the font size of the paragraph text */
    line-height: 1.6; /* Set the line-height for better readability */
    /* Add any other styles you want for the paragraph text */
  }
  
  /* Styling for the footer title */
  .footer-title {
    font-size: 16px; /* Set the font size for footer titles */
    font-weight: bold; /* Optionally set font weight */
    margin-bottom: 10px; /* Add bottom margin to create space below the title */
  }
  
  /* Styling for the unordered list (ul) in the footer */
  footer ul {
    list-style: none; /* Remove list bullets */
    padding: 0; /* Remove default padding */
    /* Add any other styles you want for the list */
  }
  
  /* Styling for the list items (li) in the footer */
  footer li {
    margin-bottom: 10px; /* Add bottom margin to create space between list items */
    /* Add any other styles you want for the list items */
  }
  
  /* Styling for the links in the footer */
  footer a {
    color: #333; /* Set the color for the links */
    text-decoration: none; /* Remove default underline */
    /* Add any other styles you want for the links */
  }
  
  /* Optional: Styling for the "Go To Top" button */
  .gotop {
    position: fixed; /* Fix the button to the viewport */
    bottom: 30px; /* Set the distance from the bottom */
    right: 30px; /* Set the distance from the right */
    background-color: #333; /* Set the background color for the button */
    color: #fff; /* Set the color for the icon */
    border: none; /* Remove border */
    padding: 10px; /* Add padding to the button */
    border-radius: 50%; /* Create a circular button */
    cursor: pointer; /* Change the cursor to indicate it's clickable */
    /* Add any other styles you want for the "Go To Top" button */
  }
  
  /* Optional: Styling for the icon inside the "Go To Top" button */
  .gotop i {
    font-size: 18px; /* Set the font size of the icon */
  }
  
  /* Optional: Styling for the button on hover */
  .gotop:hover {
    background-color: #555; /* Set the background color on hover */
  }
  

}
  